.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 500px;
  margin: auto;
  margin-top: 60px;
  border-radius: 0.3rem;
  font-family: inherit;
  background-color: grey;
}

@media screen and (max-width: 300px) {
  .login {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    margin-top: 60px;
    border-radius: 1rem;
    font-family: inherit;
  }
}

.loginTitle {
  font-size: 50px;
  font-weight: bold;
  color: #fff;
}

.loginForm {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: 70%;
}

label {
  margin: 10px 0;
  font-weight: bold;
  color: #fff;
}

.loginForm > input {
  width: 100px;
}

.loginInput {
  padding: 10px;
  background-color: #fff;
  border: none;
  font-size: 16px;
  font-weight: 400px;
}

.btn {
  margin-top: 10px;
  margin-bottom: 10px;
}

.links {
  color: #fff;
}

.error_span {
  font-size: 12px;
  color: red;
  margin-top: 10px;
  text-align: center;
}

a {
  color: silver;
  text-decoration: none;
}
